.grouped-svg {
    /*transform: translateY(150px);*/
    -webkit-animation: up-down 10s ease infinite;
            animation: up-down 10s ease infinite;
    /*animation-delay: 3s;*/
}

.svg-line {
    /* display: flex; */
    width: 20%
}

.bigger-circle {
    -webkit-animation: fade-in-fade-out infinite 2s linear;
            animation: fade-in-fade-out infinite 2s linear;
}

@-webkit-keyframes fade-in-fade-out {
    0% {
        opacity: 0;
    }

    50% {
        opacity: .6;
    }

    100% {
        opacity: 0;
    }
}

@keyframes fade-in-fade-out {
    0% {
        opacity: 0;
    }

    50% {
        opacity: .6;
    }

    100% {
        opacity: 0;
    }
}

@-webkit-keyframes ani-blink {
    0% {
        opacity: 0;
    }

    /*50% { opacity: 1; }*/
    100% {
        opacity: 1;
    }
}

@keyframes ani-blink {
    0% {
        opacity: 0;
    }

    /*50% { opacity: 1; }*/
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes up-down {
    0% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }

    20% {
        -webkit-transform: translateY(-75px);
                transform: translateY(-75px);
    }

    40% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }

    50% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    60% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }

    80% {
        -webkit-transform: translateY(-75px);
                transform: translateY(-75px);
    }

    100% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
}

@keyframes up-down {
    0% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }

    20% {
        -webkit-transform: translateY(-75px);
                transform: translateY(-75px);
    }

    40% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }

    50% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    60% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }

    80% {
        -webkit-transform: translateY(-75px);
                transform: translateY(-75px);
    }

    100% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
}

@-webkit-keyframes blur-ani {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes blur-ani {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

