.technologies{
    text-align: center;
}
.tech-header{
    font-size:25px;
    font-weight: bolder;
    margin: 35px auto;
}
.about-me{
    text-align: center;
    /* height: 100%; */
}
.about-me-wrapper{
    width:90%;
    /* padding: 10px; */
    /* border:1px solid #000; */
    margin: 0 auto;
    
    position: relative;
}
.clearfix:after { 
    content: "."; 
    visibility: hidden; 
    display: block; 
    height: 0; 
    clear: both;
 }
.about-me-header{
    font-size:25px;
    font-weight: bolder;
    margin: 35px auto;
}
.about-me img{
    float: right;
    width: 25%;
    opacity:0.5;
    -webkit-box-shadow: 2px 2px 50px -5px rgba(0,0,0,0.75);
    box-shadow: 2px 2px 50px -5px rgba(53, 51, 51, 0.75);
}
.description{
    /* float: left; */
    width: 75%;
    /* display: inline-block; */
    position: absolute;
    left: 5%;
    top: 20%;
    text-align: right;
    font-size: 10px;
    font-weight: bold;
    z-index: 10;
}

.timeline-wrapper{
    width: 80%;
    margin: 0 auto;
    position: relative;
    padding: 25px 0;
}
.timeline-wrapper:before{
    content:"";
    width:10px;
    height:100%;
    background: #b9cdd9;
    position: absolute;
}
.bar-container{
    font-size: 10px;
    padding: 10px 0;
    display: block;
    border: 1px solid black;
}
.bar-container:before{
    content: "";
    width:10px;
    height:10px;
    border-radius: 50%;
    position: absolute;
    background: red;
}
.bar-container:nth-child(odd){   
    margin: 25px 55% 25px 0;
}
.bar-container:nth-child(odd)::before{
    left:48.5%;
    border: 5px solid burlywood;
}
.bar-container:nth-child(even)::before{
    right:45%;
    border: 5px solid burlywood;
}
.bar-container:nth-child(even){
    margin: 25px 0 25px 55%;
}
.contact-me{
    width:90%;
    margin:0 auto;
    text-align: center;
}
.section-header{
    font-size: 25px;
    font-weight: bolder;
    margin: 35px auto;
}
.social{
    display: inline-block;
    width:32px;
    padding: 2%;
    border-radius: 50%;
    height:32px;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.social:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}
.social img{
    width:25px;
    height:25px;
}
.fb:hover{
    background-color: #4e609a;
}
.twitter:hover{
    background-color: #6eabeb;
}
.instagram:hover{
    background: -webkit-gradient(linear, left bottom, right top, from(#f3da58), color-stop(#c83a35), to(#aa44ab));
    background: -webkit-linear-gradient(bottom left, #f3da58, #c83a35, #aa44ab);
    background: -o-linear-gradient(bottom left, #f3da58, #c83a35, #aa44ab);
    background: linear-gradient(to top right, #f3da58, #c83a35, #aa44ab);
}
.github:hover{
    background-color: #5b2a77;
}
.linkedin:hover{
    background-color: #4973b0;
}

