    }
    .bar-container:nth-child(odd)::before{
        left:49.5%;
    }
    .bar-container:nth-child(even)::before{
        right:48.5%;
    }
    .bar-container{
        font-size: 25px;
    }
    .contact-me{
        width:20%;
    }
    .section-header{
        font-size: 60px;
    }
}


